@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
body {
  margin: 0;
  font-family: "Poppins", "Verdana", monospace, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: black !important;
}

.brand-logo,
h2 {
  font-family: "Grand Hotel", Verdana, Geneva, Tahoma, sans-serif, monospace;
}

.signUpContainer {
  height: 91vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.userImage {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  object-fit: contain;
  border: 1px solid slateblue;
}

.mycard {
  /* margin-top: 30px; */
  /* height: 91vh; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-card {
  color: salmon;
  text-align: center;
  padding: 0 20px;
  /*  margin: 20px auto; */
  max-width: 400px;
}

.loginButtonsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.guestLoginBtn {
  background-color: #e76f51;
  color: white;
  font-weight: 600;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  padding: 10px;
  margin-left: 4px;
}

.label-field {
  font-size: 16px !important;
  font-weight: 600;
  color: black;
}

.input-field input[type="text"]:focus {
  border: 1px solid rgb(8, 93, 252);
  box-shadow: 0 1px 0 0 rgb(8, 93, 252);
}

/* SignUp.js -> applying css to toast container */
#toast-container {
  top: 50px !important;
  right: 20px !important;
  left: auto !important;
}

.linkText {
  color: lightblue;
  border-bottom: 1px solid #64b5f6 !important;
  padding: 2px;
  margin: 10px 0;
  font-weight: 600 !important;
  display: block;
}

.linkText:hover {
  background-color: #64b5f6;
  transition: 500ms ease-in-out;
  color: white !important;
}

.content_loading {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
}

.home {
}

.home-card {
  margin: 20px auto;
  padding: 8px 12px;
  max-width: 500px;
  height: -webkit-max-content;
  height: max-content;
}

.postImage > img {
  width: 100%;
  max-height: 280px;
  object-fit: cover;
  object-position: center;
  border-radius: 30px !important;
  border: 1px solid slateblue;
  margin-top: 10px;
  cursor: pointer;
}

.post_bottom {
  width: 100%;
  display: flex;
  position: relative;
  justify-content: space-around;
  font-size: 20px;
  border-top: 2px solid slateblue;
  /* border: 2px solid salmon; */
  padding: 4px 0;
  margin: 8px 0;
}

.post_bottom > div {
  display: flex;
  align-items: center;
  background-color: transparent;
}

.post_bottom span {
  background-color: transparent;
  margin-left: 5px;
  font-size: 12px;
  color: var(--text);
}

.postUserName {
  text-decoration: underline;
  display: inline;
}

.postTitle {
  margin-bottom: 10px;
  font-size: large;
  font-weight: 600;
}

.postBody {
  margin-bottom: 10px;
  font-size: medium;
}

.like-btn {
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.likeIcon {
  color: transparent;
  background-color: transparent;
  border: 1px solid red;
}

.like-icon,
.post-icon {
  padding: 5px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: transparent;
}

.post-icon:hover {
  background: rgba(76, 230, 76, 0.3137254901960784);
  fill: #03c403;
}

.like-icon:hover {
  background: rgba(230, 76, 76, 0.4);
  fill: red;
}

.iconLiked {
  fill: red;
}

.iconLiked:hover {
  background: rgba(230, 76, 76, 0.5);
  fill: white;
}

.commentsSection {
  margin-top: 20px;
}

.commentsSection > p {
  font-size: 20px;
  font-weight: bold;
}

.commentItem {
  display: flex;
  flex-direction: column;
  padding: 4px;
  border-bottom: 1px solid gray;
}

.commentItem > h6 {
  font-weight: 600;
}

.sendComment {
  border: 2px solid black;
  border-radius: 10px;
  background-color: black;
  color: white;
  padding: 4px;
  margin-top: 6px;
}

.sendComment > input {
  border: none !important;
  outline: none !important;
  color: white;
  font-size: 20px;
  font-weight: 500;
}


.uploadFileCard {
  max-width: 500px;
  margin: 30px auto;
  padding: 20px;
  text-align: center;
}

.inputLabel {
  display: block;
  font-size: 16px !important;
  font-weight: bold !important;
  color: black !important;
}

.inputLabel input[type="text"],
.inputLabel textarea {
  width: 97.5%;

  border: 1px solid #9e9e9e;
  border-radius: 10px;

  margin-top: 10px;
  padding: 0px 6px;
}

.inputLabel > textarea {
  width: 100%;
  height: 5rem;
  resize: none;

  padding: 6px 8px;
}

.inputLabel > input[type="text"]:focus,
.inputLabel > textarea:focus {
  border: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}

.uploadedImage {
  max-width: 500px;
  max-height: 280px;
  object-fit: contain;
  object-position: center;
  border-radius: 30px !important;
  border: 1px solid slateblue;
  margin: 10px auto;
  cursor: pointer;
  display: flex;
}

.home {
}

.profile {
  /* max-width: 550px; */
  margin: 0px auto;
}

.profileContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 18px 0;
  border-bottom: 1px solid grey;
}

.profileImage > img {
  width: 200px;
  height: 200px;
  object-fit: contain;
  border-radius: 50%;
  border: 1px solid salmon;
}

.profileDescription {
}

.profileDescription > div {
  display: flex;
  justify-content: space-between;
  width: 108%;
}

/* .gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.item {
  width: 30%;
  margin-bottom: 20px;
} */

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 0 10px 20px 20px;
}

.itemContainer {
  margin: auto;
  margin-right: 10px;
}

.item {
  width: 100%;
  margin: 15px 0;
  border: 1px solid gray;
  border-radius: 10px;
}

.item:hover {
  transform: scale(1.03);
}

.noPostsText {
  text-align: center;
  font-family: verdana, cursive, monospace;
  font-weight: bold;
}

@media screen and (max-width: 920px) {
  .gallery {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 480px) {
  .gallery {
    grid-template-columns: 1fr 1fr;

    padding: 0 0 10px 10px;
  }
}

@media screen and (max-width: 620px) {
  .profileContainer {
    padding-bottom: 20px;
  }

  .profileImage > img {
    width: 100px;
    height: 100px;
  }

  .profileDescription {
    text-align: center;
  }

  .profileDescription > div {
    display: flex;
    flex-direction: column;
  }
}

.deleteBtn {
  float: right;
  cursor: pointer;
  position: absolute;
  right: 0px;
  top: 23px;
  color: red;
  z-index: 100;
}

.itemContainer {
  position: relative !important;
}


